*,
*::before,
*::after {
    box-sizing: border-box;
}

.main {
    padding-bottom: 50px;
}

.main__title {
    color: #fff;
    text-align: center;
    font-size: 26px;

    span {
        color: $classColor;
    }
}

.main__tabs {
    font-size: 0;
}

.main__tabs>input[type="radio"] {
    display: none;
}

.main__tabs>div {
    /* hide content by default */
    display: none;
    padding: 10px 15px;
    font-size: 16px;
}

.main__tabs>#content-1 {
    border: 1px solid $classColor;
}

.main__tabs>#content-2 {
    border: 1px solid $classColor;
}

/* show content on checked radio button (input type="radio") */
#tab-btn-1:checked~#content-1,
#tab-btn-2:checked~#content-2 {
    display: block;
}

.main__tabs>label {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    user-select: none;
    background-color: #6e6e6e;
    border: 3px solid transparent;
    padding: 2px 8px;
    font-size: 20px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 100;
    line-height: 1.5;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out;
    cursor: pointer;
    position: relative;
    top: 1px;
    width: 45%;
    opacity: 0.3;
    transition: 0.5s;
}

.main__tabs>label:not(:first-of-type) {
    border-left: none;
}

.main__tabs>input[type="radio"]:checked+label {
    position: relative;
    z-index: 1;
    background-color: black;
    border: 3px solid transparent;
    width: 55%;
    transition: 0.5s;
    box-shadow: 0 0 5px $classColor,
        0 0 5px $classColor,
        0 0 5px $classColor,
        0 0 200px $classColor;
    color: $classColor;
    opacity: 1;
}

.coming__soon {
    height: 60vh;
    font-size: 70px;
    color: rgb(0, 174, 255);
    text-align: center;
    animation: glow 3s ease-in-out infinite alternate;
}

@keyframes glow {
    from {
        color: rgb(0, 174, 255);;
    }

    to {
        color: rgb(0, 255, 76);;
    }
}



@media (max-width: 675px) {
    .main__tabs>label {
        font-size: 16px;
        padding: 4px 4px;
    }
}

@media (max-width: 550px) {
    .main__tabs>label {
        font-size: 14px;
    }
}

@media (max-width: 460px) {
    .main__title {
        font-size: 20px;
    }

    .main__tabs>label {
        span {
            display: none;
        }
    }
}

@media (max-width: 430px) {
    #content-2 {
        padding: 10px 5px !important;
    }

}

@media (max-width: 360px) {
    .main__title {
        font-size: 18px;
    }
}