.consumable__boss__list {
    padding-left: 0;
    list-style: none;
    display: grid;
}


.consumable__boss__item {
    display: grid;
    grid-template-columns: 60px 1fr 1fr 85px;
    margin-top: 10px;
    grid-column-gap: 5px;
    position: relative;
    z-index: 0;

    >div {
        background-color: rgba(32, 32, 32, .4);
        height: inherit;
        display: flex;
        justify-content: center;
        align-items: center;

        &:nth-child(1) {
            border-left: none;
        }

        &:nth-child(4) {
            p {
                font-size: 15px;
                padding-left: 5px;
            }
        }

        .boss__header {
            margin: 0;
            color: #fff;
            font-size: 20px;
            text-transform: uppercase;
            font-weight: 500;
        }
    }

    &:first-child {


        div {
            border-left: none;
            background-color: transparent;
        }
    }
}

.boss__icon {
    width: 60px;
    height: 60px;
    position: relative;

    &:hover {
        .boss__name {
            visibility: visible;
            opacity: 1;
            transition: .5s;
        }

        img {
            border: 1px solid rgb(255, 23, 255);
        }
    }

    img {
        width: 60px;
        height: 60px;
        border: 1px solid transparent;
    }
}

.boss__name {
    position: absolute;
    bottom: 105%;
    left: 0;
    width: auto;
    height: auto;
    white-space: nowrap;
    background-color: #090d21;
    padding: 8px 4px;
    border-radius: 2px;
    border: 1px solid gray;
    border-left: 1px solid gray;
    color: #fff;
    opacity: 0;
    box-shadow: 0px 0px 42px 0px magenta;
    visibility: hidden;
}

.boss__time {
    font-size: 20px;
    color: $classColor;
}

.boss__consum {
    padding: 0 5px;
    font-size: 18px;
    color: white;
    text-decoration: none;
}

.comments {
    display: flex;
    align-items: center;
    position: relative;

    span {
        font-size: 18px;
        position: absolute;
        color: #fff;
        bottom: 0;
        right: 10%;
        text-shadow: -2px -1px 3px black,
            2px 1px 3px black;
        font-weight: 700;
    }
}

.span__hover {
    position: relative;

    &:hover {
        text-decoration: underline;

        .consumable__hover {
            display: block;
            opacity: 1;
        }
    }
}

.span__green {
    color: $GreenItemColor;
}

.span__epic {
    color: $EpicItemColor;
}

.span__blue {
    color: #70B8FF;
}

.consum__img {
    width: 50px;
    height: 50px;
}

.boss__consum--hover {
    padding: 0 5px;
    font-size: 18px;
    color: white;
    font-weight: 100;
}

.back__boss,
.flip-container__boss,
.flipper__boss,
.front__boss {
    width: 100%;
    height: 60px;
}

.flip-container__boss .flipper__boss {
    transform-origin: 100% 30px;
    /* half of height */
}

.boss__time__description {
    font-size: 14px;
    color: $classColor;
    text-align: left;
}

.shiny__hover {
    cursor: pointer;

    &:hover {
        box-shadow: 0 0 5px $classColor,
            0 0 15px $classColor,
            0 0 15px $classColor,
            0 0 15px $classColor;
        background-color: rgba(32, 32, 32, 1);
        transition: 0.5s;
    }
}

@media (max-width: 1076px) {
    .hover--right {
        width: 320px;
        left: auto;
        right: 0;
        bottom: 105%;
    }
}

@media (max-width: 791px) {
    .consumable__hover {
        width: 200px;
    }
}


@media (max-width: 628px) {
    .boss__consum--hover {
        font-size: 15px;
    }
}

@media (max-width: 550px) {
    .hover__full {
        width: 300px;
        padding-right: 3px;
    }
}

@media (max-width: 533px) {
    .boss__consum--hover {
        font-size: 14px;
    }

    .consumable__boss__item {
        grid-template-columns: 60px 1fr 1fr;

        >div {
            .boss__header {
                font-size: 15px;
            }
        }
    }

    .boss__time {
        display: none !important;
    }

    .boss__time__description {
        display: none !important;
    }

    .hover--lowres {
        left: auto;
        right: 0;
        transform: translatex(30%);
        bottom: 105%;
    }
}

@media (max-width: 404px) {
    #content2 {
        padding: 10px 5px;
    }
    .hover--lowres {
        transform: translatex(20%);
    }
    .hover--lowres-left {
        transform: translatex(-30%);

    }
}

@media (max-width: 366px) {
    .boss__time__description {
        font-size: 12px;
    }
    .boss__consum--hover {
        font-size: 12px;
    }
    .hover--lowres {
        transform: translatex(10%);
    }
    .hover__full {
        width: 280px;
    }
}
