.class__menu__dropdown {
    position: relative;
    display: inline-block;
}

.class__menu__dropbutton {
    background-color: transparent;
    font-size: 18px;
    padding: 10px 5px;
    display: flex;
    outline: none;
    border: 1px solid transparent;
}

.class__menu__content {
    display: none;
    position: absolute;
    background-color: #000;
    min-width: 100px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.class__menu__dropdown:hover .class__menu__content {
    display: block;
}

.class__menu__item {
    .class__menu__dropbutton {
        position: relative;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 3px;
            height: 0;
        }

        &:hover {
            text-decoration: underline;

            &::before {
                height: 100%;
                width: 3px;
                transition: 0.4s;
            }
        }
    }

    .class__menu__content a {
        display: flex;
        color: white;
        padding: 12px 16px 12px 5px;
        text-decoration: none;
        border: 1px solid transparent;
        position: relative;
        z-index: 1;

        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
            transition: opacity 0.6s;
            z-index: -1;
        }

        &:hover {
            color: #eaffc4;

            &::after {
                opacity: 1;
            }
        }
    }
}

.class__druid {

    a:hover {
        border: 1px solid #ff7c0a;
        background: rgb(255, 124, 10);
        background: linear-gradient(274deg, rgba(255, 124, 10, 1) 0%, rgba(0, 0, 0, 1) 71%);
    }

    .class__menu__dropbutton {
        color: #ff7c0a !important;

        &::before {
            background-color: #ff7c0a !important;
        }
    }

    .class__menu__content a {
        &::after {
            border: 1px solid #ff7c0a;
            background: rgb(255, 124, 10);
