/* Flipper styles start */
.flip-container {
  perspective: 1000; }

.flip-container,
.front,
.back {
  width: 100%;
  height: 51px; }

.flipper {
  width: 100%;
  height: 100%;
  transition: 0.3s;
  transform-style: preserve-3d;
  position: relative; }

.front,
.back {
  transform: rotateX(0deg);
  display: flex;
  align-items: center;
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0; }

.front {
  z-index: 2; }

.back {
  transform: rotateX(180deg); }

.flip-container .flipper {
  transform-origin: 100% 25.5px;
  /* half of height */ }

.flip-container:hover .flipper {
  transform: rotateX(-180deg); }

