.toggle,
[id^=drop] {
  display: none; }

/* Giving a background-color to the nav container. */
nav {
  margin: 0;
  padding: 0;
  background-color: #000000;
  width: 100%; }

/* Removing padding, margin and "list-style" from the "ul",
 * and adding "position:reltive" */
nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
  width: 100%; }

/* Positioning the navigation items inline */
nav ul li {
  margin: 0px;
  width: 100%;
  background-color: #254441; }

/* Styling the links */
nav a {
  display: block;
  padding: 14px 20px;
  color: #FFF;
  font-size: 17px;
  text-decoration: none; }

nav ul li ul li:hover {
  background: #000000; }

/* Background color change on Hover */
nav a:hover {
  background-color: #000000; }

/* Hide Dropdowns by Default
 * and giving it a position of absolute */
nav ul ul {
  transition: max-height 0.1s linear;
  max-height: 0px;
  display: none;
  position: absolute;
  /* has to be the same number as the "line-height" of "nav a" */
  top: 60px; }

/* Fisrt Tier Dropdown */
nav ul ul li {
  width: 170px;
  float: none;
  display: list-item;
  position: relative; }

/* Second, Third and more Tiers	
 * We move the 2nd and 3rd etc tier dropdowns to the left
 * by the amount of the width of the first tier.
*/
nav ul ul ul li {
  position: relative;
  top: -60px;
  /* has to be the same number as the "width" of "nav ul ul li" */
  left: 170px; }

/* Media Queries
--------------------------------------------- */
@media all and (max-width: 699px) {
  nav {
    display: block;
    margin: 0; }
  /* Hide the navigation menu by default */
  /* Also hide the  */
  .toggle + a,
  .menu {
    display: none;
    /* 		transition: max-height 0.4s linear; */
    max-height: 0px; }
  /* Stylinf the toggle lable */
  .toggle {
    display: block;
    width: 100%;
    background-color: #000;
    padding: 14px 20px;
    color: #FFF;
    font-size: 17px;
    text-decoration: none;
    border: none; }
  .toggle:hover {
    background-color: #1c1c1c; }
  /* Display Dropdown when clicked on Parent Lable */
  #drop-1 + ul {
    display: block;
    max-height: 0px;
    overflow: hidden; }
  #drop-1:checked + ul {
    display: block;
    max-height: 600px;
    transition: max-height 0.4s linear; }
  [id^=drop] + ul {
    display: block;
    max-height: 0px;
    overflow: hidden; }
  [id^=drop]:checked + ul {
    display: block;
    max-height: 1600px;
    transition: max-height 0.8s linear; }
  /* Change menu item's width to 100% */
  nav ul li {
    display: block;
    width: 100%; }
  nav ul ul .toggle,
  nav ul ul a {
    padding: 0 40px;
    border: 1px solid transparent; }
  nav ul ul ul a {
    padding: 0 80px; }
  nav ul li ul li .toggle,
  nav ul ul a,
  nav ul ul ul a {
    padding: 14px 20px;
    color: #FFF;
    font-size: 17px; }
  nav ul li ul li .toggle,
  nav ul ul a {
    background-color: #1b1919; }
  /* Hide Dropdowns by Default */
  nav ul ul {
    float: none;
    position: static;
    color: #ffffff;
    /* has to be the same number as the "line-height" of "nav a" */ }
  /* Fisrt Tier Dropdown */
  nav ul ul li {
    display: block;
    width: 100%; }
  nav ul ul ul li {
    position: static;
    /* has to be the same number as the "width" of "nav ul ul li" */ } }
