.footer {
    background: #000;
}

.footer__wrap {
    display: flex;
    align-items: center;
}

.discord__logo {
    margin-right: 5px;
    margin-left: 10px;
    width: 25px;
    height: 25px;
}

.wow__logo {
    margin-right: 7px;
    margin-left: 10px;
    width: 20px;
    height: 20px;
}

.discord__profile {
    color: rgba(255, 255, 255, 0.6);
    font-size: 12px;
    font-weight: 100;
    text-transform: uppercase;
    a {
        text-decoration: none;
        text-transform: none;
        color:#68ccef;
        &:hover {
            text-decoration: underline;
        }
    }
}