.consumable__list {
    list-style: none;
    padding-left: 0;
    display: grid;
    grid-template-columns: minmax(150px, 1fr) minmax(150px, 1fr);
    grid-gap: 20px;
}

.consumable__item {
    display: flex;
    align-items: center;
    background-color: rgba(32, 32, 32, 0.4);
    border: 1px solid transparent;
    border-radius: 2px;
    position: relative;
    cursor: pointer;

    &:hover {
        box-shadow: 0 0 5px $classColor,
            0 0 15px $classColor,
            0 0 15px $classColor,
            0 0 200px $classColor;
        background-color: rgba(32, 32, 32, 1);
        transition: 0.2s;

        .consumable__hover {
            display: block;
            opacity: 1;
        }
    }
}

.consumable__icon {
    width: 49px;
    height: 49px;
    margin-right: 10px;
    position: relative;

    img {
        width: 49px;
        height: 49px;
    }

    span {
        font-size: 18px;
        position: absolute;
        color: #fff;
        bottom: 0;
        right: 10%;
        text-shadow: -2px -1px 3px black,
            2px 1px 3px black;
        font-weight: 700;
    }
}

.consumable__description {
    font-size: 18px;
    color: white;
    text-decoration: none;
}

.consumable__description--hover {
    padding: 0 5px;
    font-size: 15px;
    color: white;
    font-weight: 100;
}

.link__green {
    color: $GreenItemColor;
}

.consumable__hover {
    z-index: 5;
    display: none;
    width: 320px;
    height: auto;
    text-align: left;
    background-color: #090d21;
    padding: 8px 4px 7px 9px;
    border-radius: 10px;
    border: 1px solid gray;
    position: absolute;
    left: 0;
    bottom: 105%;
    opacity: 0;

    .consum__title {
        margin: 0;
        padding: 0;
        font-size: 14px;
        line-height: 19px;
        font-weight: 400;
        color: #fff;
    }

    .consum__level {
        margin: 0;
        padding: 0;
        font-size: 12px;
        line-height: 17px;
        color: #ffd100;
    }

    .consum__use {
        margin: 0;
        padding: 0;
        font-size: 12px;
        line-height: 17px;
        color: $GreenItemColor;
    }
}



/* Flipper styles start */
.flip-container {
    perspective: 1000;
}

.flip-container,
.front,
.back {
    width: 100%;
    height: 51px;
}

.flipper {
    width: 100%;
    height: 100%;
    transition: 0.3s;
    transform-style: preserve-3d;
    position: relative;
}

.front,
.back {
    transform: rotateX(0deg);
    display: flex;
    align-items: center;
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
}

.front {
    z-index: 2;
}

.back {
    transform: rotateX(180deg);
}

.flip-container .flipper {
    transform-origin: 100% 25.5px;
    /* half of height */
}

.flip-container:hover .flipper {
    transform: rotateX(-180deg);
}

/*!!!!!consumable__hover solution!!!!!*/
.flip-container:hover {
    position: relative;
    z-index: 1;

    .flipper {
        position: relative;
        z-index: 0;
    }
}

/*!!!!!consumable__hover solution!!!!!*/

@media (max-width: 686px) {
    .consumable__description {
        font-size: 14px;
    }
}

@media (max-width: 675px) {
    .consumable__hover-even {
        right: 0;
        left: auto;
    }
}

@media (max-width: 550px) {
    .consumable__list {
        grid-template-columns: minmax(150px, 1fr);
    }

    .consumable__description {
        font-size: 18px;
    }

    .consumable__hover-even {
        left: 0;
        right: auto;
    }
}

@media (max-width: 366px) {
    .consumable__description {
        font-size: 14px;
    }
}