.header {
  position: relative;
  z-index: 5;
  background: rgba(0, 0, 0, 0.692);

  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $classColor;
    opacity: 0.1;
  }
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo__pic {
  width: 300px;
  height: 124px;
}

.header__nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
}

.class__menu__item {
  display: flex;
  border: 1px solid transparent;
}

.class__icon {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.class__icon-spec {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}


@media (max-width: 1215px) {
  .header__wrap {
    flex-direction: column;
  }

  .header__nav {
    padding-left: 0;
  }
}

@media (min-width: 700px) and (max-width: 771px) {
  .class__mage {
    display: none;
  }
}

@media (max-width: 699px) {
  .header__nav {
    display: none;
  }
}
