#tab2 {
    position: fixed;
}

.content__menu {
    margin-top: 20px;
    text-align: center;
}

.content__menu>a,
.content__menu #tab2:target~a:nth-of-type(1),
.content__menu>div {
    border: 1px solid transparent;
    transition: 0.5s;
}

.content__menu #tab2~a:nth-of-type(1) {
    position: relative;
    z-index: 3;
    padding: 10px 60.5px;
    background-color: #000000;
    line-height: 28px;
    color: #00FF41;
    border-bottom: 1px solid #00ff41;
    letter-spacing: 5px;
    font-size: 18px;
    transition: all 300ms ease-in-out;
}

.content__menu #tab2~a:nth-of-type(2) {
    position: relative;
    padding: 10px 40px;
    box-shadow: 0 0 0 0 rgba(232, 76, 61, 0.7);
    background-color: #6e6e6e;
    animation: none;
    color: #ffffff;
    letter-spacing: 5px;
    font-size: 18px;
    transition: all 300ms ease-in-out;
}

.content__menu #tab2:target~a:nth-of-type(1) {
    background-color: #6e6e6e;
    color: #fff;
    box-shadow: none;
}

.content__menu #tab2:target~a:nth-of-type(2) {
    position: relative;
    z-index: 5;
    background-color: #000000;
    color: #ff0000;
    border-bottom: 1px solid #ff0000;
}

.content__menu>a {
    text-decoration: none;
    text-transform: uppercase;
}

#tab2,
.content__menu>div,
.content__menu #tab2:target~div:nth-of-type(1) {
    display: none;
}

.content__menu>div:nth-of-type(1),
.content__menu #tab2:target~div:nth-of-type(2) {
    display: block;
}

.menu__inner--left {
    margin-top: 10px;
}

.menu_inner--right {
    margin-top: 10px;

    .consumable__item {
        &:hover {
            box-shadow: 0 0 5px rgba(255, 0, 0, 0.438),
                0 0 15px rgba(255, 0, 0, 0.438),
                0 0 15px rgba(255, 0, 0, 0.438),
                0 0 200px rgba(255, 0, 0, 0.438);
            background-color: rgba(32, 32, 32, 1);
            transition: 0.2s;
        }
    }
}

@media (max-width: 675px) {
    .content__menu #tab2~a:nth-of-type(1) {
        font-size: 16px;
        padding: 10px 45.5px;
    }

    .content__menu #tab2~a:nth-of-type(2) {
        font-size: 16px;
        padding: 10px 27px;
    }
}

@media (max-width: 445px) {
    .content__menu #tab2~a:nth-of-type(1) {
        font-size: 14px;
        padding: 10px 20px;
    }

    .content__menu #tab2~a:nth-of-type(2) {
        font-size: 14px;
        padding: 10px 3px;
    }
}